import * as React from 'react';
import { DragSource } from 'react-dnd';
import * as ReactDOM from 'react-dom';
import { processDescription } from '../../game-logic/processDescription';
import { range, has, assign } from '../../jsUtils';
import { GameUnit } from '../../game-logic/engine';
import { ImageFill, CardSpriteProps, makeDisplayConfig } from './BaseCardSprite';
import Popup from "reactjs-popup";
import { HandCardSprite } from './HandCardSprite';
import { CELL_SIZE } from '../Board';

// START PROPS MODEL

function _isEnemy(props: CardSpriteProps) {
    return props.displayConfig.povPlayerId != props.cardData.ownerId;
}

function _uuid(props: CardSpriteProps) {
    return props.cardData.uuid;
}

function _cardBg(props: CardSpriteProps) {
    return "rgb(204, 204, 220) url(\"images/spell.png\")";
}

// END PROPS MODEL

// // props.TYPE.indexOf("Equip") > 0 ? 'url("images/sword.png")' : 'url("images/spell-book-cut.png")',
// const _cardDescriptionBoxBg = (props: UnitCardSpriteProps) => {
//     if (has(cardTypes, "spell")) {
//         return "url(\"images/spell-book-cut.png\")";
//     } else if (has(cardTypes, "equip")) {
//         return "url(\"images/sword.png\")";
//     } else {
//         return "url(\"images/pawn.png\")";
//     }
// };

const StatBox = (props: CardSpriteProps) => {
    let unitData = props.cardData;
    let borderColor = _isEnemy(props) ? 'rgba(255,0,0, 0.5)' : 'rgba(0,0,0, 0.5)';
    let backgroundColor = _isEnemy(props)  ? 'rgba(255,0,0, 0.5)' : 'rgba(0,0,0, 0.5)';
    return <div className="statbox" 
            style={{borderColor, backgroundColor, 
                marginTop: "12mm", marginLeft: "-2mm", transform: `scale(0.5)`}}>
       <div className="statline">
          {range(unitData.getUnitPower).map(i => <img key={i} className="statimage" src="images/health.png" />)}
       </div>
       <div className="statline">
         {range(unitData.getUnitRange).map(i => <img key={i} className="statimage" src="images/range.png" />)}
       </div>
    </div>
}

const cardSource = {
    beginDrag(props) {
        if (props.displayConfig.onDragStart) {
            props.displayConfig.onDragStart();
        }
        return {
            cardData: props.cardData
        };
    },
    endDrag(props) {
        if (props.displayConfig.onDragEnd) {
            props.displayConfig.onDragEnd();
        }
    }
};

const HAND_CARD_HEIGHT = CELL_SIZE * 2;
const HAND_CARD_WIDTH = HAND_CARD_HEIGHT * 0.715;

@DragSource("CARD", cardSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
}))
export class BoardCardSprite extends React.Component {
    props: CardSpriteProps; 
    render() {
        let props = this.props;
        let type = props.cardData.traits.join(" ");
        let connectDragSource = (props as any).connectDragSource;
        let borderColor = _isEnemy(props) ? 'rgba(255,0,0, 0.5)' : 'rgba(0,0,0, 0.5)';
        return connectDragSource(
            <div key={_uuid(props)}
                    onMouseOver={props.displayConfig.onMouseEnter}
                    onMouseOut={props.displayConfig.onMouseLeave}
                    onClick={props.displayConfig.onClick} style={{
                userSelect: "none",
                position: "absolute",
                backfaceVisibility: "hidden",
                // outline: "0.3mm solid black",
                // borderRadius: "0.5mm",
                background: _cardBg(props),
                width: `${props.displayConfig.width}px`,
                height: `${props.displayConfig.height}px`,
                transform: `translate3d(${props.displayConfig.x}px,${props.displayConfig.y}px,${0}px) ` +
                        `rotateZ(${props.displayConfig.rotationX}deg) rotateY(${props.displayConfig.rotationY}deg)`,
                zIndex: props.displayConfig.zIndex
            }}>
                <ImageFill image={props.cardData.imageUrl} 
                    outline={props.displayConfig.hasActionHighlight ? "1mm solid green" : undefined}
                    boxShadow={props.displayConfig.hasActionHighlight ? "0 0 12mm green" : undefined}
                    borderWidth={"0.5mm"} borderRadius={'1mm'} borderColor={borderColor}/>

                <StatBox {...props}/>
                <div className="namefield" 
                    style={{margin: "-0.5mm 0mm", width: "100%", borderRadius: '1mm', borderColor, transform: `scale(0.8)`}}>
                    <div className="name" style={{top: "0px", fontSize: `${props.cardData.name.length > 10 ? 14.5 : 20}px`}}
                        dangerouslySetInnerHTML={{__html: props.cardData.name}}/>
                </div>
                <div className="board_typebox" 
                        dangerouslySetInnerHTML={{__html: type}}/>
            </div>
        );
    }    
};



