import * as React from 'react';
import { AppState, Dispatch } from './reducers/rootReducer';
import { connect } from 'react-redux'
import HTML5Backend from 'react-dnd-html5-backend';
import {DragDropContext} from 'react-dnd';
import { GameUI } from './GameUI';
import { actions } from './reducers/uiStateActions';
import { MainScreen, MissionScreen } from '../dashboard-react/Menu';
import { startGameWithMission } from '../dashboard-react/Missions';

export const GameUIContainer = (connect as any)(
    appState => ({appState}), 
    dispatch => ({dispatch}))(GameUI);

@DragDropContext(HTML5Backend)
class App extends React.Component {
    props: {appState: AppState, dispatch: Dispatch};
    public render() {
        const {appState, dispatch} = this.props;
        const menuFunction = this.props.appState.uiState.menuFunction || missionScreen;

        const missionSequence = [
            {
                description: "A simple assignment",
                mission: "configs/mission1.6x6.yaml"
            },
            {
                description: "A simple assignment",
                mission: "configs/mission1.6x6.yaml"
            }
        ];
        return menuFunction();

        // function mainMenu() {
        //     return <MainScreen clickCatArmy={() => dispatch(actions.setUIMenuFunction(catMissionScreen))}/>;
        // }
        function missionScreen() {
            const startMission = (missionObj) => {
                startGameWithMission(missionObj);
                dispatch(actions.setUIMenuFunction(gameState));
            };
            return <MissionScreen startMission={startMission} missionSequence={missionSequence}/>
        }
        function gameState() {
            return <div style={{position: "fixed", top: "50%", left: "50%"}}>
                <GameUIContainer x={0} y={0} restart={switchToMainMenu} next={switchToMainMenu}/>
            </div>;
            function switchToMainMenu() {
                return dispatch(actions.setUIMenuFunction(missionScreen));
            }
        }
    }
}

export const AppContainer = (connect as any)(
    appState => ({appState}), 
    dispatch => ({dispatch}))(App);



