type ZoneProps = CardSpriteProps[];

interface GameUIProps {
    boardProps: ZoneProps;
    graveyardProps: ZoneProps[];
    playerHandProps: ZoneProps[];
    boardHighlightProps: ZoneProps;
    deckProps: ZoneProps[];
}

interface ExtendedGameUIProps extends GameUIProps {
    x, y: number;
    dispatch: Dispatch;
}

function appStateToGameUIProps(dispatch, appState: AppState): GameUIProps {
    const uiState = appState.uiState;
    const {gameState, legalActions} = appState.gameStateAndHistory;
    const players = gameState ? getGamePlayers(gameState) : [];
    return {
        boardProps: boardStateToZoneProps(dispatch, appState),
        graveyardProps: players.map(player => playerStateToGraveyardZoneProps({uiState, player})),
        deckProps: players.map(player => playerStateToDeckZoneProps({uiState, player})),
        boardHighlightProps: actionStateToBoardHighlightProps(dispatch, {gameState, uiState, legalActions}),
        playerHandProps: players.map(player => playerStateToHandZoneProps(dispatch, {uiState, legalActions, player}))
    };
} 

function EndTurnButton({x, y, appState, dispatch, restart, next}) {
    let {legalActions, gameState} = appState.gameStateAndHistory as GlobalGameState;
    if (!(gameState as undefined|GameState)) {
        return <div/>;
    }
    const style = {
        backgroundColor: gameState.currentPlayerId === 0 ? legalActions.length <= 1 ? "#4CAF50" : "#929220" : "gray",
        disabled: gameState.currentPlayerId === 1,
        // border: "none",
        color: "white",
        padding: "15px 32px",
        textAlign: "center",
        textDecoration: "none",
        display: "inline-block",
        transform: `translate3d(${x}px, ${y}px, 0px)`,
        fontSize: "16px",
        width: "100px",
        height: "100px",
        zIndex: 100
    }
    if (gameState.gameIsWon && gameState.winnerPlayerId === 0) {
        return <button onClick={next} style={style as any} type="button"> Next Mission </button>;
    } else if (gameState.gameIsWon && gameState.winnerPlayerId === 1) {
        return <button onClick={restart} style={style as any} type="button"> Restart </button>;
    }
    return <button onClick={() => dispatchEndTurnAction(dispatch, appState)} 
                            style={style as any} type="button"> End Turn </button>;
}

function dispatchEndTurnAction(dispatch, {gameStateAndHistory: {legalActions}}: AppState) {
    for (let {action} of legalActions) {
        if (action.type === "EndTurnAction") {
            executeGameAction(dispatch, action);
            setTimeout(queueNextAiMove, 1000);
            break;
        }
    }
    return;
    async function queueNextAiMove() {
        let state = await gameAIMove(dispatch);
        if (state.currentPlayerId === 1) {
            setTimeout(queueNextAiMove, 1);
        }
    }
}

function appStateToResourceBars({gameStateAndHistory: {gameState}}: AppState) {
    if (!gameState) {
        return [];
    }
    const p1:GamePlayer = gameState.player1;
    const p2:GamePlayer = gameState.player2;
    let p1Text = "", p2Text = "";
    const {gameIsWon, winnerPlayerId} = gameState;
    if (gameIsWon && winnerPlayerId === 0) {
        p1Text = `${p1.victoryPoints}/${p1.targetVictoryPoints}. You have won!`;
        p2Text = `${p2.victoryPoints}/${p2.targetVictoryPoints}. You have lost!`;
    } else if (gameIsWon && winnerPlayerId === 1) {
        p1Text = `${p1.victoryPoints}/${p1.targetVictoryPoints}. You have lost!`;
        p2Text = `${p2.victoryPoints}/${p2.targetVictoryPoints}. You have won!`;
    } else {
        p1Text = `${p1.victoryPoints} of ${p1.targetVictoryPoints} needed`;
        p2Text = `${p2.victoryPoints} of ${p2.targetVictoryPoints} needed`;
    }

    return [
        <div className="victory-points" key="victory-points1" style={{zIndex: 100, position: 'absolute', 
            height: "21.198195mm", width: "113mm",
            pointerEvents: 'none',
            top: `${CELL_SIZE*3.2}px`, 
            transform: "translate3d(-50%, 0%, 0) translate3d(400px, 0, 0) scale(0.5)"}}> 
                Victory points: <br/>
                {p1Text}
                <ResourceCountRaw gold={p1.gold} mana={p1.fame}/>
        </div>,
        <div className="victory-points" key="victory-points2" style={{zIndex: 100, position: 'absolute', 
            height: "21.198195mm", width: "113mm",
            pointerEvents: 'none',
            top: `${-CELL_SIZE*3.2}px`, 
            transform: "translate3d(-50%, -100%, 0) translate3d(400px, 0, 0) scale(0.5)"}}> 
                Victory points: <br/>
                {p2Text}
                <ResourceCountRaw gold={p2.gold} mana={p2.fame}/>
        </div>
    ];
}

export function GameUI({x, y, appState, dispatch, restart, next}) {
    const {
        boardProps, playerHandProps, 
        graveyardProps, boardHighlightProps, 
        deckProps
    } = appStateToGameUIProps(dispatch, appState);

    const {gameStateAndHistory: {gameState}}: AppState = appState;
    const rs:RenderSettings = {
        cellSize: CELL_SIZE,
        boardWidth: gameState ? gameState.boardWidth : 5,
        boardHeight: gameState ? gameState.boardHeight : 5,
        boardFlipped: false
    };

    let zones = [
        boardProps,
        boardHighlightProps,
        ...playerHandProps,
        ...graveyardProps,
        ...deckProps,
    ];

    let cards = ([] as any).concat(
        ...zones.map(props => props.map(cardSprite))
    );

    return [
        <Board key="board" rs={rs} x={x} y={y}/>,
        ...cards,
        <EndTurnButton key="button" {...{
            x: CELL_SIZE * 3 + 10, 
            y: CELL_SIZE * 2 + 10, 
            appState, dispatch, restart, next}}/>,
        ...appStateToResourceBars(appState)
    ];
    // Helpers
    function cardSprite(props: CardSpriteProps) {
        let uuid = toUuid(props.cardData, props.displayConfig.zone);
        // if (uuids[uuid]) {
        //     console.log(uuid);
        //     console.log({"before": uuids[uuid], after: props});
        //     throw new Error(uuids[uuid] + " VS " + JSON.stringify(props));
        // }
        // uuids[uuid] = props;
        if (props.displayConfig.zone == "highlight") {
            return <HighlightSprite key={uuid} {...props}/>;
        }
        if (props.displayConfig.zone == "board-tooltip") {
            return <HandCardSprite 
                    key={toUuid(props.cardData, props.displayConfig.zone)} 
                    {...props}/>;
        }
        return <CardContainer
            key={uuid}
            dispatch={dispatch}
            SpriteClass={zoneToSpriteClass(props.displayConfig.zone)}
            spriteProps={props}
        />;
    }
    function toUuid(cardData: GameCard, zone: string) {
        if (zone === "highlight") {
            return cardData.uuid + "-highlight-" + cardData.x + "-" + cardData.y;
        }
        if (zone === "board-tooltip") {
            return cardData.uuid + "-board-tooltip";
        }
        return cardData.uuid;
    }
    function zoneToSpriteClass(zone: string) {
        switch (zone) {
            case "highlight": return HighlightSprite;
            case "board": return BoardCardSprite;
            default: return HandCardSprite;
        }
    }
}

function getSoloAction({uuid}, actions: GameActionNode[]) {
    for (let {action, subactions} of actions) {
        if (subactions) {
            continue;
        }
        if (action.targets.length === 0 && action.sources.length == 1 && action.sources[0].source.uuid === uuid) {
            return action;
        }
    }
    return undefined;
}

function isActionSource(uuid: number, actions: GameActionNode[]): boolean {
    for (let {action} of actions) {
        for (const k of Object.keys(action)) {
            if (k.indexOf("source") == 0) {
                if (action[k].uuid == uuid) {
                    // Matched the first source of an action
                    return true
                }
                break; // Only check first
            }
        }
    }
    return false;
}

// BOARD UI CONFIG

function boardStateToZoneProps(dispatch, {uiState: {povPlayerId, cardUIStates, draggingUuid}, gameStateAndHistory: {gameState, legalActions}}: AppState): ZoneProps {
    if (!gameState) {
        return [];
    }
    const {boardWidth, boardHeight} = gameState;
    const board_offset_x = -CELL_SIZE * boardWidth / 2, board_offset_y = -CELL_SIZE * boardHeight / 2;

    let gameUnits = getGameUnits(gameState);
    let cardSpriteProps:CardSpriteProps[] = [];
    for (let gameUnit of gameUnits) {
        makeProps(gameUnit);
    }
    return cardSpriteProps;
    // Helpers:
    function makeProps(unit: GameUnit) {
        if (povPlayerId == 0) {
            var x = board_offset_x + unit.x * CELL_SIZE, y = board_offset_y + (boardHeight - unit.y - 1) * CELL_SIZE;
        } else {
            var x = board_offset_x + unit.x * CELL_SIZE, y = board_offset_y + unit.y * CELL_SIZE;
        }
        const zone = "board";
        const isHovered = (cardUIStates[unit.uuid] || {}).hoverZone == zone;
        let soloAction = getSoloAction(unit, legalActions);
        cardSpriteProps.push({
            displayConfig: makeDisplayConfig({
                zone, x, y, povPlayerId,
                rotationX: unit.isUnitReady ? 0 : 90,
                width: CELL_SIZE, height: CELL_SIZE, isHovered,
                onClick: () => {
                    if (gameState.currentPlayerId === 1) gameAIMove(dispatch);
                    else if (soloAction) {
                        gameStateActions.executeGameAction(dispatch, soloAction);
                    }
                },
                hasActionHighlight: isActionSource(unit.uuid, legalActions),
                onMouseEnter: () => {dispatch(actions.setCardHoverState(unit.uuid, zone))},
                onMouseLeave: () => {dispatch(actions.setCardHoverState(unit.uuid, undefined))},
                onDragStart: () => {dispatch(actions.setDragTarget(unit.uuid))},
                onDragEnd: () =>  {dispatch(actions.setDragTarget(undefined))},
            }),
            cardData: unit
        });
        if (isHovered) {
            const OFFSET_X = CELL_SIZE * 3, OFFSET_Y = -unit.ownerId * HAND_CARD_HEIGHT;
            cardSpriteProps.push({
                displayConfig: makeDisplayConfig({
                    zone: "board-tooltip",
                    // x: x + CELL_SIZE, y: y - HAND_CARD_HEIGHT/2, 
                    povPlayerId,
                    x: OFFSET_X, y: OFFSET_Y,
                    opacity: 1,
                    miscStyle: {
                        pointerEvents: "none"
                    },
                    width: HAND_CARD_WIDTH, height: HAND_CARD_HEIGHT,
                    zIndex: 101
                }),
                cardData: unit
            });
        }
    }
}

const HAND_CARD_HEIGHT = CELL_SIZE * 2;
const HAND_CARD_WIDTH = HAND_CARD_HEIGHT * 0.715;

function playerStateToGraveyardZoneProps({uiState: {povPlayerId}, player}): ZoneProps {
    let graveyardCards = getGraveyardCards(player);
    const OFFSET_X = CELL_SIZE * 3 + HAND_CARD_WIDTH, OFFSET_Y = -player.id * HAND_CARD_HEIGHT;
    return graveyardCards.map(cardSpriteProp);
    // Helpers:
    function cardSpriteProp(card: GameCard, index: number) {
        let {x, y, z} = stack(index);
        x += OFFSET_X, y += OFFSET_Y;
        return {
            displayConfig: makeDisplayConfig({
                zone: "graveyard",
                x, y, povPlayerId,
                width: HAND_CARD_WIDTH, height: HAND_CARD_HEIGHT,
                zIndex: z
            }),
            // type hack
            cardData: card as (GameCard & GameUnit)
        };
    }
    function stack(i) {
        return {
            x: i * 1.5,
            y: i * 1.5,
            z: i
        };
    }
}

function playerStateToDeckZoneProps({uiState: {povPlayerId}, player}): ZoneProps {
    let deckCards = getDeckCards(player);
    const OFFSET_X = CELL_SIZE * 3, OFFSET_Y = -player.id * HAND_CARD_HEIGHT;
    return deckCards.map(cardSpriteProp);
    // Helpers:
    function cardSpriteProp(card: GameCard, index: number) {
        let {x, y, z} = stack(index);
        x += OFFSET_X, y += OFFSET_Y;
        return {
            displayConfig: makeDisplayConfig({
                zone: "deck",
                x, y, povPlayerId,
                width: HAND_CARD_WIDTH, height: HAND_CARD_HEIGHT,
                zIndex: z,
                rotationY: 180
            }),
            // type hack
            cardData: card as (GameCard & GameUnit)
        };
    }
    function stack(i) {
        return {
            x: i * 1.5,
            y: i * 1.5,
            z: i
        };
    }
}

function flattenTree(
    nodes: GameActionNode[], 
    actionStack: GameAction[] = [], 
    into: GameAction[][] = []
): GameAction[][] {
    for (const node of nodes) {
        const newStack = append(actionStack, node.action);
        if (node.subactions) {
            flattenTree(node.subactions, newStack, into);
        } else {
            into.push(newStack);
        }
    }
    return into;
}

function actionStateToBoardHighlightProps(
    dispatch, {
        uiState: {
            povPlayerId,
            cardUIStates,
            draggingUuid
        },
        legalActions,
        gameState
    }
): ZoneProps {
    const boardWidth = gameState ? gameState.boardWidth : 5;
    const boardHeight = gameState ? gameState.boardHeight : 5;
    let cardSpriteProps:CardSpriteProps[] = [];
    const board_offset_x = -CELL_SIZE * boardWidth / 2, board_offset_y = -CELL_SIZE * boardHeight / 2;
    for (let actionList of flattenTree(legalActions)) {
        const leafAction = actionList[actionList.length - 1];
        let source = leafAction.sources[0]; 
        if (source && leafAction.targets.length > 0) {
            let uiState = cardUIStates[source.source.uuid] || {};
            if (draggingUuid ? source.source.uuid == draggingUuid : !!uiState.hoverZone) {
                cardSpriteProps.push(cardSpriteProp(source.source, actionList));
            }
        }
    }
    return cardSpriteProps;
    // Helpers:
    function cardSpriteProp(source: GameCard, actionList: GameAction[]) {
        const leafAction = actionList[actionList.length - 1];
        let {targetUnit, targetSquare} = leafAction as any;
        let target = targetUnit || targetSquare;
        if (povPlayerId == 0) {
            var x = board_offset_x + target.x * CELL_SIZE, y = board_offset_y + (boardHeight - target.y - 1) * CELL_SIZE
        } else {
            var x = board_offset_x + target.x * CELL_SIZE, y = board_offset_y + target.y * CELL_SIZE
        }
        return {
            displayConfig: makeDisplayConfig({
                zone: "highlight",
                x, y, povPlayerId,
                width: CELL_SIZE, height: CELL_SIZE,
                opacity: 0.4,
                zIndex: 100,
                onDrop: async () => {
                    console.log("ON DROP");
                    for (const action of actionList) {
                        console.log("ON EXECUTE");
                        await action.execute();
                    }
                    console.log("ON SYNC STATE");
                    await gameStateActions.syncState(dispatch);
                }
            }),
            // type hack
            cardData: assign(source, {uuid: source.uuid + "-" + JSON.stringify(leafAction)})
        };
    }
}

function hasHover(card, cardUIStates, zone) {
    return (cardUIStates[card.uuid] || {}).hoverZone == zone;
}

function playerStateToHandZoneProps(dispatch, {uiState: {draggingUuid, openInformation, cardUIStates, povPlayerId}, legalActions, player}): ZoneProps {
    let handCards = getHandCards(player);
    return handCards.map(cardSpriteProp);
    // Helpers:
    function cardSpriteProp(card: GameCard, index: number) {
        // let x = OFFSET_X + unit.x * CELL_SIZE, y = OFFSET_Y + unit.y * CELL_SIZE;
        let {x,y,z} = handFan(index, handCards.length);
        const zone = "hand";
        const isHovered = !draggingUuid && hasHover(card, cardUIStates, zone);
        if (isHovered) {
            y += povPlayerId === player.id ? -HAND_CARD_HEIGHT : HAND_CARD_HEIGHT;
            z += 300;
        } else if (!draggingUuid && anyTrue(handCards, card => hasHover(card, cardUIStates, zone))) {
            y += povPlayerId === player.id ? -HAND_CARD_HEIGHT/2 : HAND_CARD_HEIGHT/2;
            z += 200;
        }

        return {
            displayConfig: makeDisplayConfig({
                zone,
                x, y, povPlayerId,
                rotationY: povPlayerId === player.id ? 0 : 180, // || openInformation ? 0 : 180,
                rotationX: povPlayerId === player.id || isHovered ? 0 : 180,
                width: HAND_CARD_WIDTH, height: HAND_CARD_HEIGHT,
                zIndex: z,
                hasActionHighlight: isActionSource(card.uuid, legalActions),
                onMouseEnter: () => {dispatch(actions.setCardHoverState(card.uuid, zone))},
                onMouseLeave: () => {dispatch(actions.setCardHoverState(card.uuid, undefined))},
                onDragStart: () => {dispatch(actions.setDragTarget(card.uuid))},
                onDragEnd: () =>  {dispatch(actions.setDragTarget(undefined))},
                onClick: async () => {
                    const action = getSoloAction(card, legalActions);
                    if (action) {
                        console.log("DO ACTION")
                        await action.execute();
                        await gameStateActions.syncState(dispatch);
                    } else
                    console.log("DONT ACTION")
                }
            }),
            // type hack
            cardData: card as (GameCard & GameUnit)
        };
    }
    function handFan(i, n) {
        let angleStart = Math.PI/4, angleEnd = Math.PI*3/4;

        let angle = interpolate(angleStart, angleEnd, i /Math.max(n-1, 1));
        let width = n * HAND_CARD_WIDTH / 2, height=HAND_CARD_HEIGHT / 7;
        return {
            x: -Math.cos(angle) * width - HAND_CARD_WIDTH / 2,
            y: (povPlayerId === player.id ? -1 : 1) * (Math.sin(angle) * height - CELL_SIZE * 4.5) - HAND_CARD_HEIGHT / 2,
            z: i
        }
    }
}
