export var CardUnit = (props) => (<div className="cardunit" style={props.style}>
    <div className="cardunit-back"/>
    <div className="descriptiontext" dangerouslySetInnerHTML={{__html: props.DESCRIPTION}}
         style={{borderColor: "rgba(0,20,0,1.0)",
            backgroundImage: `url("images/${props.TYPE.indexOf("Unique ") === -1 ? 'pawn' : 'star-unique'}.png")`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            backgroundSize: 'contain'
         }}/>
    <div className="flavourtext" dangerouslySetInnerHTML={{__html: props.FLAVOURTEXT}}/>
    <div className="typebox" dangerouslySetInnerHTML={{__html: props.TYPE.replace("Unique ", "")}}/>
    <ImageFill image={props.IMAGE_FILE} borderColor="rgba(0,20,0,1.0)" borderRadius="1mm"/>
    <div className="statbox" 
            style={{transform: "scale(1.6)"}}>
      {/* <div className="statline">
         {range(props.HEALTH).map(i => <img key={i} className="statimage" src="images/health.png" />)}
      </div> */}
      <div className="statline">
        {range(props.RANGE).map(i => <img key={i} className="statimage" src="images/range.png" />)}
      </div>
    </div>
    <div className="namefield" style={{borderRadius: '1mm', borderColor: "rgba(0,0,0,1.0)"}}>
      <div className="name" dangerouslySetInnerHTML={{__html: props.NAME}}/>
    </div>
    {CostBox(props)}
    {PowerBox(props)}
  </div>);

const cardSource = {
    beginDrag(props) {
        if (props.displayConfig.onDragStart) {
            props.displayConfig.onDragStart();
        }
        return {
            cardData: props.cardData
        };
    },
    endDrag(props) {
        if (props.displayConfig.onDragEnd) {
            props.displayConfig.onDragEnd();
        }
    }
};

@DragSource("CARD", cardSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
}))
export class HandCardSprite extends React.Component {
    props: CardSpriteProps;

    render() {
        let {displayConfig} = this.props;
        let cardData = this.props.cardData as GameCard;

        if (has(cardData.traits, "Spell") || has(cardData.traits, "Equip")) {
            var CardType = CardSpell;
        } else {
            var CardType = CardUnit;
        }

        let cardObject = <CardType
            NAME={cardData.name}
            gold={cardData.cost}
            style={{
                position: "absolute",
                // outline: displayConfig.hasActionHighlight ? "1mm solid green" : undefined,
                boxShadow: displayConfig.hasActionHighlight ? "0 0 12mm green" : undefined
            }}
            TYPE={cardData.traits.join(" ")}
            DESCRIPTION={processDescription(cardData.description)}
            FLAVOURTEXT={processDescription(cardData.flavourText)}
            IMAGE_FILE={cardData.imageUrl}
            // TODO: Simplify these
            power={cardData.isUnit ? cardData.getUnitPower || cardData.power : 0}
            RANGE={cardData.isUnit ? cardData.getUnitRange || cardData.range : 0}
        />;

        // TODO better way of encoding this, hacky for now
        const OWIDTH = 238.11023622 + 1.8897637795 *2, OHEIGHT = 332.5984252 + 1.8897637795 *2;
        const SCALE = displayConfig.height / OHEIGHT;
        // x&y displacement to deal with being scaled
        const DX = -(1 - SCALE) * OWIDTH/ 2, DY = -(1 - SCALE) * OHEIGHT/ 2;
        const x = displayConfig.x, y = displayConfig.y;
        let connectDragSource = (this.props as any).connectDragSource;
        return connectDragSource(<div className="card"
            onClick={displayConfig.onClick}
            onMouseEnter={displayConfig.onMouseEnter}
            onMouseLeave={(this.props as any).isDragging ? undefined : displayConfig.onMouseLeave}
            style={assign({
                userSelect: "none",
                position: "absolute",
                opacity: displayConfig.opacity,
                pointerEvents: (this.props as any).isDragging ? 'none' : undefined,
                // backfaceVisibility: "hidden",
                // borderRadius: "0.5mm",
                // background: _cardBg(this.props),
                width: `${displayConfig.width}px`,
                height: `${displayConfig.height}px`,
                transform: `translate3d(${x}px,${y}px,${0}px) ` + 
                        `rotateZ(${displayConfig.rotationX}deg) rotateY(${displayConfig.rotationY}deg)`,
                zIndex: displayConfig.zIndex
                // marginRight,
                // position,
                // outline,
                // width: `${width}px`,
                // height: `${height}px`,
                // display: "inline-block",
                // overflow: "visible",
                // backfaceVisibility: 'hidden',
                // zIndex,
                // opacity,
        }, displayConfig.miscStyle) as any}> {cardWithBacking()} </div>);
        // Helpers
        function cardWithBacking() {
            const {rotationX, rotationY} = displayConfig;
            let faceDown = rotationY > 90;
            return <>
                {displayConfig.rotationY !== 0 ? <div
                    className={faceDown ? "front" : "back"}
                    style={{
                        width: `${displayConfig.width}px`, 
                        height: `${displayConfig.height}px`, 
                        backgroundImage: "url('cards/back.svg')",
                        zIndex: 1,
                        backgroundSize: '100% 100%'}}
                /> : undefined}
        
                <div
                    className={faceDown ? "back" : "front"}
                    style={{
                        width: `${displayConfig.width}px`, 
                        height: `${displayConfig.height}px`, 
                        transform: `scale(${SCALE}) translate3d(${DX}px,${DY}px,${0}px)`,
                        // backgroundImage: "url('cards/back.svg')", 
                        zIndex: 0,
                        backgroundSize: '100% 100%'}}
                >
                    {cardObject}
                </div>
            </>; 
        }
    }
    // translate(${-width/2}px, ${-height/2}px)
};



